.login {
    height: 100vh;
    width: 100vw;
    background-color: rgb(20, 2, 38);
    overflow-y: hidden;
    overflow-x: hidden;
}

.login form {
    margin: 20vh 0 0 25vw;
    height: 50vh;
    width: 40vw;
    background-color: azure;
    padding: 5vh 5vw 5vh 5vw;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
}

.login form input[type='email'] {
    padding: 7px;

}

.login form label {
    margin: 1vh 0 1vh 0;
    font-weight: bolder;
}

.login form h2 {
    margin: 2vh 0 2vh 17vw;
    color: rgb(2, 53, 116);
    text-decoration: underline;
}

.login form p {
    height: 1vh;
    color: red;
    font-weight: 750;
    font-size: 0.8rem;
    margin: 0 0 1vh 1vw;
}

.login form button {
    width: 10vw;
    height: auto;
    padding: 8px;
    font-size: 1.2rem;
    color: rgb(36, 36, 171);
    font-weight: bolder;
    margin: 2vh 0 0 15vw;
}

.login form button:hover {
    background-color: yellowgreen;
    cursor: pointer;
}

.login form input[type='password'] {
    padding: 7px;
}

.login form span{
    margin: 2vh 0 0 13.5vw;
}