.register {
    height: 100vh;
    width: 100vw;
    background-color: rgb(20, 2, 38);
    overflow-y: hidden;
    overflow-x: hidden;
}

.register form {
    margin: 20vh 0 0 25vw;
    height: 50vh;
    width: 40vw;
    background-color: azure;
    padding: 5vh 5vw 5vh 5vw;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
}

.register form input[type='email'] {
    padding: 7px;

}

.register form label {
    margin: 1vh 0 1vh 0;
    font-weight: bolder;
}

.register form h2 {
    margin: 1vh 0 1vh 17vw;
    color: rgb(2, 53, 116);
    text-decoration: underline;
}

.register form p {
    height: 1vh;
    color: red;
    font-weight: 750;
    font-size: 0.7rem;
    margin: 0 0 2vh 0.5vw;
}

.register form button {
    width: 10vw;
    height: auto;
    padding: 8px;
    font-size: 1.2rem;
    color: rgb(36, 36, 171);
    font-weight: bolder;
    margin: 2vh 0 0 15vw;
}

.register form button:hover {
    background-color: yellowgreen;
    cursor: pointer;
}

.register form input[type='password'] {
    padding: 7px;
}

